import React from "react"
import Layout from "../../components/layout"
import SEO from "../../components/seo"

const MarcoLetras = (dataState) => (
  <Layout>

    <SEO  
    title="Marcos con mensajes"
    meta="Te explicamos como hacer un cuadro con letras en 3D. Es un regalo muy original para una amiga que estamos seguros que le va a gustar."
    description="Te explicamos como hacer un cuadro con letras en 3D. Es un regalo muy original para una amiga que estamos seguros que le va a gustar."
    lang="es"
    />

    <div class="postContainer">
    <p>&nbsp;</p>
    <h1>Marcos con mensajes</h1>
<p><span >&nbsp;</span></p>
<p><span >Los marcos con mensajes son un regalo muy económico tanto si los quieres comprar como si los quieres hacer tu misma. La idea del regalo es un marco de foto pero en vez de contener una foto contendrá letras con relieve, formando un mensaje para tu amiga. Es un regalo muy original por que no habrá ninguno como el que hagas. Por una parte tendrás el marco que hay de muchísimos tipos y colores y por otra parte las letras que las hay de distintos tipos y colores. Podrás combinar letras de distintos tipos y colores.</span></p>
<p><span >&nbsp;</span></p>
<p><span >Puedes comprar el marco y las letras que quieras, y en casa montar las letras dentro del cuadro, pegándolas con cola liquida blanca. O también puedes crear el marco y las letras con cartón y cartulinas, aquí te vamos a explicar como. Es un regalo donde hay un sinfín de posibilidades y el único limite que hay es tu imaginación.</span></p>
<p><span >&nbsp;</span></p>
<h2>Marcos</h2>
<p><span >Este vídeo te explica como hacer un marco con cartón cartulinas y pegamento. En el vídeo se utiliza silicona caliente pero puedes comprar pegamento blanco líquido.</span></p>
<p><span >&nbsp;</span></p>
<p>
  
  <p><iframe class="video" title="1007" src="https://www.youtube.com/embed/lGrWKMwB02A" data-mce-fragment="1"></iframe></p>
</p>
<p><span >&nbsp;</span></p>
<p><span >Este vídeo te explica como hacer marcos originales con discos,confeti y tela. Te recomendamos que lo veas, son muy originales.</span></p>
<p><span >&nbsp;</span></p>
<p>
  <p><iframe class="video" title="1006" src="https://www.youtube.com/embed/DqLkpI4G_78" data-mce-fragment="1"></iframe></p>
</p>
<p><span >&nbsp;</span></p>
<h2>Letras</h2>
<p><span >Estamos seguros que los vídeos anteriores te han ayudado a hacerte una idea de como se deberían hacer los marcos. Pero sino, estamos seguros que si buscas por internet encontraras más ideas de como hacerlos y incluso con otros materiales.</span></p>
<p><span >Las letras van a darle un toque distintivo a tu cuadro. Dependiendo de la tipografía y de los colores de las letras el cuadro transmitirá unas sensación o otra. Si utilizas letras de distintos tamaños y de colores muy vivos transmitiras alegría. En canvio por ejemplo si utilizas un fondo de cuadro blanco y letras del mismo tamaño y negras transmitiras elegancia y sobriedad. Todo tiene que estar en relación con el mensaje que quieres mostrarle a tu amiga y de como sea ella. Que tipo de cuadro crees que le gustaría mas?</span></p>
<p><span >Las letras igual que los cuadros se pueden comprar pero aquí te vamos a dar ideas de como hacerlas tu misma.</span></p>
<p><span >En este vídeo en ingles se muestra como hacer letras con tres dimensiones. Utiliza cartón y una pistola de silicona para unir la partes. No es necesaria la pistola de silicona. Con pegamento blanco basta para unir las partes. Las letras que te enseñan en el vídeo son muy gruesas. En nuestro caso tendrán que ser mas pequeñas tanto de tamaño como de grosor, sino no nos cabrán dentro del cuadro.</span></p>
<p>
  <p><iframe class="video" title="1005" src="https://www.youtube.com/embed/FU2Rd8Y8U1w" data-mce-fragment="1"></iframe></p>
</p>
<p><span >Este vídeo muestra como hacer las letras pero con cartulinas. En nuestro caso es mas recomendable hacer las letras con cartulinas que con cartón por que van a ser letras pequeñas. En cambio si quisiésemos hacer letras mas grandes utilizariamos el cartón.</span></p>
<p>
  <p><iframe class="video" title="1004" src="https://www.youtube.com/embed/Fg5gZgs0Xzk" data-mce-fragment="1"></iframe></p>
</p>

</div>
    <div style={{ height:`300px`}}>
    </div>
  </Layout>
)

export default MarcoLetras
